import React from "react";
import theme1 from "./theme_img/.jpg";
import theme2 from "./theme_img/.jpg";
import theme3 from "./theme_img/.jpg";
import theme4 from "./theme_img/.jpg";
import theme5 from "./theme_img/.jpg";
import theme6 from "./theme_img/.jpg";
import theme7 from "./theme_img/.webp";
import theme8 from "./theme_img/.png";
import { Link } from "react-router-dom";

const themes = [
  { src: theme1, alt: "", label: "" },
  { src: theme2, alt: "", label: "" },
  { src: theme3, alt: "", label: "" },
  { src: theme4, alt: "", label: "" },
  { src: theme5, alt: "", label: " " },
  { src: theme6, alt: " ", label: " " },
  { src: theme7, alt: "", label: "" },
  { src: theme8, alt: "", label: "" },
];

const ThemeItem = (
  { src, alt, label } //  
) => (
  <div style={{ width: "240px", position: "relative" }}>
    <Link to="/" style={{ textDecoration: "none" }}>
      <div
        style={{
          position: "relative",
          display: "block",
          width: "210px",
          height: "210px",
          margin: "0 20px",
          borderRadius: "50%",
          overflow: "hidden",
        }}
      >
        <img
          src={src}
          alt={alt}
          style={{
            position: "absolute",
            left: "50%",
            width: "auto",
            height: "100%",
            transform: "translate(-50%, 0)",
            filter: "brightness(0.7)",
          }}
        />
        <em
          style={{
            position: "absolute",
            top: "0",
            left: "50%",
            display: "flex",
            width: "210px",
            height: "210px",
            transform: "translate(-50%, 0)",
            zIndex: "1",
            fontSize: "24px",
            color: "#fff",
            alignItems: "center",
            justifyContent: "center",
            fontWeight: "bold",
          }}
        >
          {label}
        </em>
      </div>
    </Link>
  </div>
);

const ImageList = () => {
  return (
    <>
      <div
        style={{
          width: "80%",
          position: "relative",
          height: "210px",
          marginTop: "50px",
          display: "flex",
          justifyContent: "space-around",
          alignItems: "center",
        }}
      >
        {themes.map(
          (theme, index) => index < 4 && <ThemeItem key={index} {...theme} /> //  1 ~ 4,   
        )}
      </div>
      <div
        style={{
          width: "80%",
          position: "relative",
          height: "210px",
          marginTop: "120px",
          display: "flex",
          justifyContent: "space-around",
          alignItems: "center",
        }}
      >
        {themes.map(
          (theme, index) => index >= 4 && <ThemeItem key={index} {...theme} /> //  5 ~ 8
        )}
      </div>
    </>
  );
};

export default ImageList;
